import React, {Component} from 'react';

//props: datasize, width, height

export default class Visualizer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      numBars: props.defaultNumBars,
      barHeights: []
    };
  }
  
  randomInt(min, max) {
    return Math.floor(Math.random() * (max-min+1) + min);
  }

  componentDidMount() {
    this.randomize();
  }

  setNumBars(num) {
    this.setState({numBars: num});
  }

  randomize() {
    let bars = [];
    const maxHeight = window.innerHeight * 2/3;
    console.log(maxHeight);
    for (let i = 0; i < this.state.numBars; i++) {
      bars[i] = this.randomInt(5, maxHeight);
    }
    this.setState({barHeights: bars});
  }

  render() {
    let width;
    let padding;
    if (this.state.numBars === 10) {
      width = 100;
      padding = 10;
    }
    else if (this.state.numBars === 50) {
      width = 25;
      padding = 5;
    }
    else if (this.state.numBars === 100) {
      width = 15;
      padding = 3;
    }
    else {
      width = 2;
      padding = 1.75;
    }

    return (
      <>
        {this.state.barHeights.map((value, idx) => (
          <div className="visBar bg-gradient-to-t from-blue-400 to-blue-200 inline-block select-none shadow-md mr-0"
              key={idx}
              style={{height: `${value}px`, width: `${width}px`, marginLeft: `${padding/2}px`, marginRight: `${padding/2}px`}}/>
        ))}
      </>
    );
  }
}